<template>
    <div class="detail">
         <van-nav-bar
                title="详情"
                left-text="返回"
                @click-left="back"
                left-arrow
         />
         <!-- 宠物内容 -->
         <div class="detail_content">

             <!-- 宠物图片 -->
              <div class="detail_img">
                  <img class="auto-img" :src="petsData.coverURL"/>
              </div>

              <!-- 宠物详细信息 -->
              <div class="desc_box">
                  <div class="Pets_nameAndLink clearfix">
                      <div class="pets_name fl">
                          <span>{{petsData.name}}</span>
                          <span>({{petsData.engName}})</span>
                      </div>
                      <div class="pets_link fr">
                          <img :src="require('../assets/images/' + (petsData.Collection ? 'like_active.png' : 'like.png'))" @click="myLike(petsData)"/>
                      </div>
                  </div>

                  <!-- 信息 -->
                  <div class="pets_information">
                      <div class="pets_features">
                          <div class="pets_information_title">性格特点</div>
                          <div class=" clearfix">
                              <span class="pets_features_1 fl">聪明机灵</span>
                               <span class="pets_features_1 fl">聪明</span>
                             
                          </div>
                      </div>
                      <div class="pets_desc">
                          <div class="pets_information_title">宠物信息</div>
                          <div class="pets_desc_content clearfix">
                              <div class="pets_from" v-for="(item,index) in mainData" :key="index">
                                  <span>{{item.dataName}}：</span>
                                  <span>{{item.data}}</span>
                              </div>
                              <!-- <div class="pets_from">
                                  <span>易患病：</span>
                                  <span>肠胃疾病</span>
                              </div>
                               <div class="pets_from">
                                  <span>寿命：</span>
                                  <span>9-15年</span>
                              </div>
                              <div class="pets_from">
                                  <span>价格：</span>
                                  <span>2000-4000元</span>
                              </div> -->
                          </div>
                          <div class="pets_desc_sum">
                              <div class="pets_desc_title" v-for="(item,index) in descData" :key="index">
                                  <span class="pets_desc_title_1">{{item.dataName}}：</span>
                                  <span>{{item.data}}</span>
                              </div>
                           
                          </div>
                      </div>
                  </div>

              </div>
         </div>
    </div>
</template>

<script>
import {createNamespacedHelpers} from 'vuex'

const {mapState, mapMutations} = createNamespacedHelpers('homeModules')

    export default {
        data(){
            return {
                name: null,
                listType: null,
                titleStyle: "",
                mainData:[],
                descData:[],
                petsData: {},
                saveAddNum: null,
                //查找myLikeData所要删除的数据下标
                addIndex: null
            }
        },
        computed:{
             ...mapState(['petsStyleData','type','mySearchData','myLikeData'])
        },
        created(){
            //截取查询参数
            this.name = this.$route.query.name;
            this.listType=this.$route.query.type;
            this.titleStyle=this.$route.query.styleName;
            this.changepetsData();
        },
        methods: {
            ...mapMutations(['addmyLikeData','changeLike','removemyLikeData']),
            // 返回上一级
            back(){
                this.$router.go(-1);
            },
            changepetsData(){
                let obj={};
                let addNum=0;
                let petsSumData=null;
                if(this.titleStyle=="mySearchData"){
                     addNum=0;
                     let petsSearchData = this.mySearchData;
                     petsSearchData.forEach(v=>{
                        if(v.name==this.name){
                            obj=v;
                            this.saveAddNum=addNum;
                        }
                         addNum++;
                    })
                }else{
                    addNum=0;
                    petsSumData = Object.assign({},this.petsStyleData);
                    petsSumData[this.listType].petsMaxData.forEach(v=>{
                        if(v.name==this.name){
                            obj=v;
                            this.saveAddNum=addNum;
                        }
                        addNum++;
                    })
                }
                
                
               
                // this.petsData=obj;
                obj.characters.split('、');
                this.petsData=obj;
                // this.mainData.push({"nationName": '祖籍',"nation": this.petsData.nation});
                let arr=['祖籍','易患病','寿命','价格'];
                let arr1=['描述','体型特征','特点','照顾须知','喂养注意']
                let add=0;
                let add1=0;
                for(let pro in this.petsData){
                    if(pro=='nation' || pro=='easyOfDisease'||pro=='life'||pro=='price'){
                        if(this.petsData[pro]!=''){
                             this.mainData.push({dataName:arr[add],data:this.petsData[pro]});
                        }
                        add++;
                       
                    }

                    if(pro=='desc' || pro=='feature'||pro=='characterFeature'||pro=='careKnowledge'||pro=='feedPoints'){
                        if(this.petsData[pro]!=''){
                            this.descData.push({dataName:arr1[add1],data:this.petsData[pro]});
                            
                        }
                        add1++;
                       
                    }
                }
            },
            myLike(item){
                if(localStorage.getItem('__Tk')==null){
                    this.$dialog.confirm({
                        title: '请先登录再进行收藏',
                        message: '是否立刻进行登录',
                    })
                    .then(() => {
                        this.$router.push({name: "Login"})
                    })
                    .catch(() => {
                    });
                    return;
                }
                //petsMaxData
                
                //  let like= state[data.type][data.num].petsMaxData[data.listNum].Collection;
               
                if(!this.petsData.Collection){
                    if(this.titleStyle=='petsStyleData'){
                       this.changeLike({type:this.titleStyle,num:this.listType,listNum:this.saveAddNum}); 
                        
                    }else if(this.titleStyle=='mySearchData'){
                        
                        let addNM=this.mySearchData;
                       this.changeLike({type:this.titleStyle,num:this.listType});
                    }
                    this.addmyLikeData(this.petsData);
                    //  this.petsData.Collection=true;
                }else{
                    let addnum=0;
                    this.myLikeData.forEach(v=>{
                        if(v.name==this.name){
                            this.addIndex=addnum;
                        }
                        addnum++;
                    })
                     this.changeLike({type:this.titleStyle,num:this.listType,listNum:this.saveAddNum}); 
                    this.removemyLikeData(this.addIndex);
                    
                }
                this.petsData.Collection=!this.petsData.Collection;
                
               
            }
        }
    }
</script>

<style lang="less" scoped>
    .detail_content{
        position: fixed;
        top: 46px;
        left: 0px;
        right: 0px;
        bottom: 0px;
        padding: 10px;
        overflow-y: auto;

       .Pets_nameAndLink{
           padding-top: 10px;
           height: 32px;
           line-height: 32px;
       }
       .pets_name{
           font-size: 16px;
           font-weight: 500;
       }
    }
    .pets_features{
        padding: 10px 0px;
        border-bottom: 1px solid #d0d0d0;

        .pets_features_1{
             padding: 5px;
             display: block;
             margin: 5px;
             border-radius: 16px;
             color: #fff;
             background-color: skyblue;
        }
    }
    .pets_information_title{
        padding: 5px 0px;
        font-size: 14px;
        text-align: center;
    }
    .pets_desc_content{
        .pets_from{
            float: left;
            margin: 6px;
            padding: 5px;
            color: #fff;
            border-radius: 17px;
            background-color: skyblue;
        }
    }
    .pets_desc_sum{
        padding-top: 10px;
    }
    .pets_desc_title{
        font-size: 13px;
        // line-height: 5px;
         letter-spacing:2px;
        padding-bottom: 8px;

        .pets_desc_title_1{
            font-weight: 900;
        }
    }
</style>